.feed {
  margin-top: 190px;
}

.delete-btn-wrap {
  position: relative;
}

.feed-wrap {
  padding: 16px;
  background: var(--brown-10);
  border-radius: 16px;
  border: 1px solid var(--brown-30);
}

.total-count {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  padding-bottom: 16px;
  color: var(--brown-40);
  font-size: 1.25rem;
}

.btn-link {
  position: absolute;
  top: -52px;
  right: 0;
  display: flex;
  justify-content: flex-end;
  text-align: center;
  margin: 10px 0 9px;
}

.btn-link > button {
  display: block;
  width: 100px;
  height: 35px;
  color: #fff;
  border: 1px solid var(--brown-40, rgba(207 207 207 1));
  background-color: var(--brown-40);
  box-shadow: 0 4px 4px 0 rgb(0 0 0 / 25%);
  border-radius: 200px;
  line-height: 25px;
}

.to-list-btn {
  position: fixed;
  right: 24px;
  bottom: 24px;
  text-align: center;
}

.to-list-btn a {
  display: block;
}

.share-sns {
  z-index: 1;
}

@media (width <= 1200px) {
  .btn-link {
    right: 32px;
  }
}

@media (width <= 768px) {
  .feed {
    margin-top: 175px;
  }

  .total-count {
    font-size: 1.125rem;
  }
}
